@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

/*Configuración inicial*/

*{
    margin: 0;
    padding: 0;
}

body{
    background-color: skyblue;
}

/*Configuración del contenedor principal de la app*/

.principal-container{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

/*Configuración del título de la app*/

.image-title{
    margin-top: 25px;
}

/*Configuración del cuerpo del PokeDex*/

.pokedex-btn-cont{
    background-color: rgb(148, 19, 19);
    width: 50%;
    border-top: 1px solid #fff;    margin:0 auto;
    border-left: 1px solid #fff;
    border-right: 1px solid #fff;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;

    display: flex;
    flex-direction: row;
    align-items: center;
}

.pokedex-cont{
    background-color: rgb(148, 19, 19);
    width: 50%;
    padding-bottom: 80px;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    border-left: 1px solid #fff;
    border-right: 1px solid #fff;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;

    display: flex;
    flex-direction: column;
    align-items: center;
}

/*Configuración botones del PokeDex*/

.btn-little-cont{
    display: flex;
    flex-direction: row;
}

.blue-btn{
    background-color: skyblue;
    width: 70px;
    height: 70px;
    border-radius: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 20px;
    border: 10px solid #fff;
}

.red-btn{
    background-color: red;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 20px;
    border: 2px solid #fff;
}

.yellow-btn{
    background-color: yellow;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 20px;
    border: 2px solid #fff;
}

.green-btn{
    background-color: green;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 20px;
    border: 2px solid #fff;
}

/*Configuración pantalla PokeDex*/

.border-screen{
    background-color: rgb(206, 191, 191);
    width: 80%;
    margin: 0 auto;
    margin-top: 25px;
    margin-bottom: 25px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 5px;
}

.pokedex-screen{
    background-color: #000;
    width: 80%;
    height: 300px;
    margin: 0 auto;
    margin-top: 25px;
    margin-bottom: 25px;
    border-radius: 5px;

    display: flex;
    flex-direction: row;
}

.data-container{
    width: 50%;
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    padding-top: 70px;
    color: #fff;
    font-family: 'Ubuntu', sans-serif;
}

.name{
    font-size: 25px;
}

.name-pokemon{
    font-size: 30px;
    font-weight: bolder;
    margin-bottom: 10px;
}

/*Configuración imagen de Pokemón*/

.image-pokemon{
    width: 50%;
}

/*Configuración del boton*/

.poke-btn{
    background-color: rgb(140, 214, 29);
    width: 25%;
    border: none;
    border-radius: 5px;
    padding: 10px 10px;
    font-family: 'Ubuntu', sans-serif;
    font-size: 25px;
}

.creator{
    font-family: 'Ubuntu', sans-serif;
    margin-bottom: 25px;
}

/*------------------------Configuración media query para dispositivos móviles------------------------*/

@media (max-width: 600px){

/*Configuración del título de la app*/

.image-title{
    width: 60%;
}

/*Configuración del cuerpo del PokeDex*/

.pokedex-btn-cont{
    width: 90%;
}

.pokedex-cont{
    width: 90%;
    padding-bottom: 40px;
}

/*Configuración botones del PokeDex*/

.btn-little-cont{
    display: flex;
    flex-direction: row;
}

.blue-btn{
    width: 50px;
    height: 50px;
}

.red-btn{
    width: 20px;
    height: 20px;
}

.yellow-btn{
    width: 20px;
    height: 20px;
}

.green-btn{
    width: 20px;
    height: 20px;
}

/*Configuración pantalla PokeDex*/

.border-screen{
    width: 90%;
    padding-top: 10px;
    padding-bottom: 10px;
}

.pokedex-screen{
    width: 90%;
    height: 340px;
    flex-direction: column;
    align-content: center;
}

.data-container{
    width: 100%;
    padding-left: 0px;
    padding-top: 10px;
    padding-bottom: 25px;
}

/*Configuración imagen de Pokemón*/

.image-pokemon{
    width: 50%;
    margin: 0 auto;
}

/*Configuración del boton*/

.poke-btn{
    width: 40%;
}   

}/*Finalización media query*/

/*=============================================================================================*/

@media (min-width: 600px){

/*Configuración del título de la app*/

.image-title{
    width: 70%;
}

/*Configuración del cuerpo del PokeDex*/

.pokedex-btn-cont{
    width: 90%;
}

.pokedex-cont{
    width: 90%;
}

/*Configuración imagen de Pokemón*/

.image-pokemon{
    width: 40%;
}
    
}/*Finalización media query*/

/*=============================================================================================*/

@media (min-width: 900px){

/*Configuración del título de la app*/
    
.image-title{
    width: 70%;
}
    
/*Configuración del cuerpo del PokeDex*/
    
.pokedex-btn-cont{
    width: 60%;
}
    
.pokedex-cont{
    width: 60%;
}
    
/*Configuración imagen de Pokemón*/
    
.image-pokemon{
    width: 40%;
}

/*Configuración del boton*/

.poke-btn{
    width: 30%;
} 
        
}/*Finalización media query*/

/*=============================================================================================*/

@media (min-width: 1200px){

/*Configuración del título de la app*/
        
.image-title{
    width: 70%;
}
        
/*Configuración del cuerpo del PokeDex*/
        
.pokedex-btn-cont{
    width: 50%;
}
        
.pokedex-cont{
    width: 50%;
}
            
}/*Finalización media query*/